import React from "react";
import {Link} from "react-router-dom";
import * as S from "./style";

const WebPage = () => {
  return (
    <>
      <S.MainWrapper>
        <S.Img src="../result-img/web.png" id="img1" />
        <S.Toppercent>60% </S.Toppercent>
        <S.Midpercent>30% </S.Midpercent>
        <S.Bottompercent>10% </S.Bottompercent>
        <S.Developer1></S.Developer1>
        <S.Developer2></S.Developer2>
        <S.Developer3></S.Developer3>
        <S.Developer4></S.Developer4>
        <S.Developer5></S.Developer5>
        <Link to="/" style={{textDecoration: "none"}}>
          <S.Home src="../result-img/home-white.png" id="img2" />
        </Link>
        <S.Share src="../result-img/share-white.png" id="img3" />
      </S.MainWrapper>
    </>
  );
};

export default WebPage;
